import navigation from '../../components/navigation/navigation';
export default {
	data() {
		return {
			config: {
				type: 1, //0无title，1有title
				title: "我的",
				back: false
			},
			code: '',
			userId: '',
			avatar: '',
			name: '',
			mobile: '',
			firstTime: true,
			messages: [],
			
		}
	},
	components: {
		navigation
	},
	filters: {
		filters1: function(arg) {
			if (arg == 0) {
				return '申请你的资源了'
			} else if (arg == 1) {
				return '申请加入你的队伍';
			} else if (arg == 2) {
				return '已同意你的合租入队申请';
			} else if (arg == 3) {
				return '退出了你的队伍';
			} else if (arg == 4) {
				return '已将你们的队伍解散';
			} else if (arg == 5) {
				return '已同意你的下单申请';
			} else if (arg == 6) {
				return '回复了你的评论';
			} else if (arg == 7) {
				return '评论了你的帖子';
			}
		},
	},
	onLoad() {
		if (uni.getStorageSync('userId') && uni.getStorageSync('token')) {
			this.userId = uni.getStorageSync('userId')
			this.getuser()
		} else {
			let _this = this
			console.log(1)
			uni.login({
				provider: 'weixin',
				success: function(response) {
					console.log(response)
					_this.code = response.code
				},
				fail: (err) => {
					console.log(err)
				}
			})
		}
	},
	onShow(){
		if (uni.getStorageSync('userId') && uni.getStorageSync('token')) {
			this.getuser()
			this.getmessage()
		}
	},
	onPullDownRefresh: function() {
		//下拉刷新的时候请求一次数据
		if (uni.getStorageSync('userId') && uni.getStorageSync('token')) {
			this.getuser()
			this.getmessage()
		}
	},
	methods: {
		goEdit: function() {
			uni.navigateTo({
				url: '../my/editMine'
			})
		},
		goFinish: function() {
			uni.navigateTo({
				url: '../my/finishOrder'
			})
		},
		goUnfinish: function() {
			uni.navigateTo({
				url: '../my/unfinishOrder'
			})
		},
		gotoother(e){
			if (this.userId!=e){
				uni.navigateTo({
					url:'../my/other?userId='+e
				})
			}
		},
		//获取微信头像昵称
		getuserinfo(e) {
			console.log(2)
			console.log(e.detail.userInfo);
			let user = e.detail.userInfo;
			this.name = user.nickName;
			this.avatar = user.avatarUrl;
			let url = 'https://tjuyjn.top:1314/api/login/'
			let _this = this
			uni.request({
				url: url,
				method: 'POST',
				data: {
					code: _this.code
				},
				success: res => {
					console.log(res.data);
					_this.userId = res.data.userId
					_this.firstTime = res.data.firstTime
					uni.setStorageSync('token', res.data.token)
					uni.setStorageSync('userId', res.data.userId)
					if (!res.data.firstTime) {
						_this.getuser()
						_this.getmessage()
					} else {
						_this.sendfirst()
					}
					uni.setStorageSync('avatar', _this.avatar)
					uni.setStorageSync('name', _this.name)
				},
				fail: err => {

				}
			})
		},
		//获取数据库中头像昵称
		getuser() {
			this.$http.get('/api/person/info/', {
				userId: this.userId
			}).then(res => {
				this.name = res.name;
				this.avatar = res.avatarUrl;
				if (!res.mobile) {
					setTimeout(() => {
						uni.showToast({
							title: '请先绑定手机号',
							icon: 'none'
						});
					}, 100);
					uni.navigateTo({
						url: '../my/sms'
					})
				} else {
					this.mobile = res.mobile
				}
			}).catch((e) => {});
		},
		sendfirst() {
			this.$http.post('/api/person/update/', {
				userId: this.userId,
				name: this.name,
				sex: null,
				introduce: null,
				avatarUrl: this.avatar,
			}).then(res => {
				uni.showToast({
					title: '登录成功',
					icon: 'none'
				})
			}).catch((e) => {});
		},
		getmessage(){
			this.$http.get('/api/message/getAll/', {
				userId: this.userId
			}).then(res => {
				this.messages = res.data.messages
			}).catch((e) => {});
		},
		agreejoin(e){
			let _this = this
			uni.showModal({
				title: '提示',
				content: '是否确认同意加入你的队伍',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						_this.$http.post('/api/detail/processApply/', {
							applyId:e
						}).then(res => {
							console.log(res)
							uni.showToast({
								title: '操作成功',
								icon: 'success'
							});
							_this.getmessage()
						}).catch(err => {
							console.log(err)
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		gotodetail(e){
			uni.navigateTo({
				url:'../order/detail?Id='+e +'&type='+0
			})
		}
	}
}
